.App {
  text-align: center;
 
  justify-items:center;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Container4 {
  overflow-y: auto;
  box-shadow: 5px 5px 10px 1px #000000;
  min-width: 70vw;
  max-height: 80vh;

border-radius:60px;
display: grid;
background-color: rgba(160, 117, 89, 0.774);
justify-content: center;
justify-items:center;

scrollbar-width: thin; /* thin, auto, or none - for Firefox */
scrollbar-color: #888 #cc6433; /* thumb color, track color - for Firefox */
}

.Container4::-webkit-scrollbar {
width: 12px; /* width of the scrollbar */
}

.Container4::-webkit-scrollbar-track {
background: #f1f1f1; /* color of the track */
}

.Container4::-webkit-scrollbar-thumb {
background: #888; /* color of the thumb */
}

.Container4::-webkit-scrollbar-thumb:hover {
background: #555; /* color of the thumb on hover */
}

.content {

align-items: center;
  justify-items:center;
}

.MainContainer {
  min-height: 90vh;
  min-width: 70vw;
border-radius: 60;
align-items: center;
  justify-items: center;
}
.background {
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Row{
  justify-items: start;
min-width:70vw;
