  min-height: 2vh;
  
  
}


.divier {
  
  color:rgb(148, 96, 0);
  border-color: rgb(160, 104, 0);
  background-color: rgb(160, 104, 0);
    display: block;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    margin-left: auto;
    margin-right: auto;
    border-style: inset;
    height:2px;
    border-width: 1px;
  width: 60vw;
}

.mini-container {
  align-items: center;
  justify-items: center;
  margin-left: 40px;
  box-shadow: 5px 5px 10px 1px #000000;
  min-height: 10vh;
  min-width: 16vw;
  border-radius: 40px;
  transition: transform 0.3s; /* Add transition for smooth effect */
}

.mini-container:hover {
  transform: scale(1.1); /* Enlarge the container on hover */
}


.Grid {
  display:grid;
min-height: 20vh;
grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Adjust the width as needed */
  gap: 20px;


}

